.preloader-box {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #020201;
    position: fixed;
    position: 0 0 0 0;
    z-index: 100;
}

.preloader {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}